.container {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-77%, -50%);
}

.container__input {
  width: 240px;
  height: 50px;
  background-color: var(--white-button);
  text-align: right;
  color: var(--grey-text);
  border: 1px solid #0000;
  border-radius: 5px;
  margin-left: 50px;
  padding: 18px;
  font-size: 20px;
  line-height: 24px;
  display: block;
}

.container__input:focus, .container__input:hover {
  outline: none;
}

.container__button {
  width: 50px;
  height: 50px;
  background-color: #0000;
  background-color: var(--grey-button);
  cursor: pointer;
  color: #6f6f6f80;
  border: #0000;
  border-radius: 4px 0 0 4px;
  font-size: 22px;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.container__button:hover, .container__button:focus {
  color: #6f6f6fe6;
}

.container__list {
  width: 290px;
  height: 320px;
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.container__item {
  width: 50px;
  height: 40px;
  background-color: var(--grey-button);
  cursor: pointer;
  color: var(--grey-text);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  transition: all .3s;
  display: flex;
}

.container__item:hover {
  background-color: var(--grey-button-hover);
}

.container__item--number {
  background-color: var(--white-button);
  font-size: 26px;
  line-height: 31px;
}

.container__item--number:hover {
  background-color: var(--white-button-hover);
}

.container__item--equal {
  background-color: var(--blue-button);
  color: var(--white-button);
  font-size: 26px;
  line-height: 31px;
}

.container__item--equal:hover {
  background-color: var(--blue-button-hover);
}

.container__item--input {
  box-sizing: content-box;
  width: 290px;
  height: 50px;
  background-color: var(--grey-button);
  border: 1px solid var(--grey-button);
  margin-bottom: 10px;
  display: block;
  position: relative;
}

.container__item--input:hover, .container__item--input:focus {
  background-color: var(--grey-button);
}

.container__item--clear {
  width: 110px;
  padding: 14px;
  font-size: 10.4px;
  line-height: 15px;
}

.opacity {
  opacity: .4;
}

.history {
  width: 300px;
  height: 320px;
  margin-right: 40px;
}

@media screen and (max-width: 1000px) {
  .history {
    display: none;
  }
}

.history__item {
  color: #6f6f6fcc;
  text-align: right;
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 15px;
}

.animate {
  animation: animationOnMove 1s linear;
}

@keyframes animationOnMove {
  0% {
    opacity: 0;
    margin-top: -31px;
  }

  100% {
    opacity: 1;
    margin-top: 0;
  }
}

:root {
  --grey-button: #e6e6e6;
  --white-button: #fff;
  --blue-button: #28bae8;
  --grey-button-hover: #d7d7d7;
  --white-button-hover: #f5f5f5;
  --blue-button-hover: #1aa9d4;
  --body-bg: #f8fbff;
  --grey-text: #6f6f6f;
}

* {
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--body-bg);
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 20px;
}

h1, h2, h3, h4, h5, h6, p, ul, li, a {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

