.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-77%, -50%);
  display: flex;
  &__input {
    display: block;
    width: 240px;
    height: 50px;
    background-color: var(--white-button);
    border: 1px solid transparent;
    border-radius: 5px;
    padding: 18px;
    text-align: right;
    font-size: 20px;
    line-height: 24px;
    color: var(--grey-text);
    margin-left: 50px;
    &:focus,
    &:hover {
      outline: none;
    }
  }
  &__button {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: transparent;
    width: 50px;
    height: 50px;
    background-color: var(--grey-button);
    border: transparent;
    cursor: pointer;
    color: rgba(111, 111, 111, 0.5);
    font-size: 22px;
    transition: 0.3s;
    border-radius: 4px 0 0 4px;
    &:hover,
    &:focus {
      color: rgba(111, 111, 111, 0.9);
    }
  }
}
.container__list {
  width: 290px;
  height: 320px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.container__item {
  width: 50px;
  height: 40px;
  background-color: var(--grey-button);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  cursor: pointer;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: 'Inter', sans-serif;
  color: var(--grey-text);
  transition: 0.3s;
  &:hover {
    background-color: var(--grey-button-hover);
  }
  &--number {
    font-size: 26px;
    line-height: 31px;
    background-color: var(--white-button);
    &:hover {
      background-color: var(--white-button-hover);
    }
  }
  &--equal {
    font-size: 26px;
    line-height: 31px;
    background-color: var(--blue-button);
    color: var(--white-button);
    &:hover {
      background-color: var(--blue-button-hover);
    }
  }
  &--input {
    box-sizing: content-box;
    display: block;
    width: 290px;
    height: 50px;
    background-color: var(--grey-button);
    border: 1px solid var(--grey-button);
    position: relative;
    margin-bottom: 10px;
    &:hover,
    &:focus {
      background-color: var(--grey-button);
    }
  }
  &--clear {
    font-size: 10.4px;
    line-height: 15px;
    width: 110px;
    padding: 14px;
  }
}

.opacity {
  opacity: 0.4;
}

// ----------------------------

.history {
  width: 300px;
  height: 320px;
  margin-right: 40px;
  @media screen and (max-width: 1000px) {
    display: none;
  }
}
.history__item {
  font-size: 18px;
  line-height: 15px;
  color: rgba(111, 111, 111, 0.8);
  margin-bottom: 16px;
  text-align: right;
}

.animate {
  animation: animationOnMove 1s linear;
}

@keyframes animationOnMove {
  0% {
    margin-top: -31px;
    opacity: 0%;
  }
  100% {
    margin-top: 0px;
    opacity: 100%;
  }
}
